}

/* form starting stylings ------------------------------- */

.Input_input__group__hU2Xf {
    position: relative;
    margin-bottom:2rem;
    margin-top:3rem;
}

.Input_animatedInput__2bIwx {
    font-size: 1.8rem;
    padding: 1rem 1rem 1rem 5px;
    display: block;
    width: 45rem;
    border: none;
    border-bottom: 1px solid #757575;
    margin-top: 2rem;
}

.Input_animatedInput__2bIwx:focus {
    outline: none;
}

/* LABEL ======================================= */

.Input_animatedInputLabel__3YyhD {
    color: #333333;
    font-size: 1.8rem;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 1px;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

/* active state */

.Input_animatedInput__2bIwx:focus~label, .Input_animatedInput__2bIwx:valid~label {
    top: -1.3rem;
    left: 0;
    font-size: 1.6rem;
    color: #333333;
}

/* BOTTOM BARS ================================= */

.Input_bar__zYCXq {
    position: relative;
    display: block;
    width: 45rem;
}

.Input_bar__zYCXq:before, .Input_bar__zYCXq:after {
    content: '';
    height: 2px;
    width: 0;
    bottom: 1px;
    position: absolute;
    background:var(--color-secondary-dark);
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.Input_bar__zYCXq:before {
    left: 50%;
}

.Input_bar__zYCXq:after {
    right: 50%;
}

/* active state */

.Input_animatedInput__2bIwx:focus~.Input_bar__zYCXq:before, .Input_animatedInput__2bIwx:focus~.Input_bar__zYCXq:after {
    width: 50%;
}

/* HIGHLIGHTER ================================== */

.Input_highlight__2drwp {
    position: absolute;
    height: 50%;
    width: 10rem;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
}

/* active state */

.Input_animatedInput__2bIwx:focus~.Input_highlight__2drwp {
    animation: Input_inputHighlighter__2wwqt 0.3s ease;
}

/* ANIMATIONS ================ */

@keyframes Input_inputHighlighter__2wwqt {
    from {
        background: var(--color-secondary-dark);
    }
    to {
        width: 0;
        background: transparent;
    }
}
