.login-page{
    background-image: url('../../assets/bg-md.jpg');
    min-height:100vh;
    /* min-width: 100%; */
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
      /* Add the blur effect */
  /* filter: blur(8px);
  -webkit-filter: blur(8px); */
    display:flex;
    align-items: center;
    justify-content:center;
}

.login-box{
    background-color: #ffffff;
    min-height: 42rem;
    width: 50rem;
    padding:2.5rem;
    border-radius: .5rem;
    position: relative;
}
.login-page__logo,.login-page__heading{
    margin-top:2rem !important;
    margin-bottom:2rem !important;
}
.login-page__heading{
    margin-bottom: .5rem !important;
}
.login-page__input-box{
    border: 1px solid transparent;
    width: min-content;
    margin: auto auto;
}
.login-page__btn{
    padding: 1rem 3rem;
    font-size: 2rem !important;
    letter-spacing:1.2px;
    position: absolute;
    right: 2.5rem;
}
.login-page__signup-btn-box{
    display: flex;
    justify-content:space-between;
    align-items: center;
    margin-bottom:1rem
}